body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face{font-family:"Montserrat";font-style:normal;font-weight:400;font-display:swap;src:local("Montserrat Regular"),local("Montserrat-Regular"),url(https://fonts.gstatic.com/s/montserrat/v14/JTUSjIg1_i6t8kCHKm459Wlhyw.woff2) format("woff2");unicode-range:U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD}.homepage{display:flex;height:90vh;background:white;margin:0px}.jumbotron{display:flex;height:80vh;width:100%;background-image:linear-gradient(to right top, #577cce, #4766bc, #3851aa, #2a3c98, #1d2785, #2b2182, #361b7e, #3f127a, #5b1b85, #75268f, #8e3198, #a73ea2);background-size:cover;background-position:center;justify-content:flex-start;color:white;align-items:center}.jumbotron h2{font-size:4em;text-shadow:2px 2px #a64ac9}@media only screen and (min-width: 600px){.jumbotron h2{font-size:6em}}@media only screen and (min-width: 1200px){.jumbotron h2{font-size:6em}}.wrapper{display:flex;position:absolute;top:0px;left:0px;height:100vh;width:100%;position:fixed;background:rgba(0,0,0,0.7);justify-content:center;align-items:center;z-index:5}.wrapper .screen{display:flex;flex-direction:column;justify-content:center;align-items:center;height:300px;width:300px;background:white;border-radius:20px;box-shadow:6px 6px 3px gray}.wrapper .close{position:absolute;top:10px;right:10px;color:white;cursor:pointer}.wrapper .Questionaire{display:flex;flex-direction:column;justify-content:center;align-items:center}.wrapper .Questionaire form{display:flex;flex-direction:column}.wrapper .Questionaire h2{background:linear-gradient(to right, #17e9e0, #00e3e8, #00dcef, #00d5f4, #00cef7);width:100%;color:white;padding:5px 0px;text-align:center;border-radius:5px;box-shadow:4px 4px 3px gray}.navbar{display:flex;justify-content:center;height:10vh;width:100%;font-family:"Montserrat";font-weight:800;color:black;background:white}.navbar .logo{display:none;width:20%;justify-content:center;align-items:center}.navbar .logo p{font-size:0.5em;color:black}.navbar .logo p:hover{-webkit-transform:scale(1.1);transform:scale(1.1);transition:0.2s}.navbar .logo p:active{-webkit-transform:scale(0.9);transform:scale(0.9);transition:0.2s}.navbar .navbar-items{display:flex;align-items:center;justify-content:flex-end;width:80%}.navbar .navbar-items p{color:black;font-size:0.8em;padding:0 2px;cursor:pointer}.navbar .navbar-items p:hover{-webkit-transform:scale(1.1);transform:scale(1.1);transition:0.2s}.navbar .navbar-items p:active{-webkit-transform:scale(0.9);transform:scale(0.9);transition:0.2s}@media only screen and (min-width: 600px){.navbar{margin-left:0px}.navbar .navbar-items p{padding:0 15px}.navbar .logo{display:flex;width:20%;justify-content:center;align-items:center}.navbar .logo p{font-size:1.2em;color:black}.navbar-items{font-size:1.2em;width:100%}}.Help,.About{background:white;display:flex;height:90vh;margin:0px;justify-content:center;align-items:center}.Help .text,.About .text{display:flex;font-size:0.7em;flex-direction:column;justify-content:center;text-align:center;height:300px;width:800px}.Help .text h2,.About .text h2{color:white;background:linear-gradient(to right, #17e9e0, #00e3e8, #00dcef, #00d5f4, #00cef7);box-shadow:5px 5px 10px gray;padding:10px;border-radius:10px;text-shadow:2px 2px gray}@media only screen and (min-width: 1200px){.Help .text,.About .text{font-size:1em}}.Help{background:white;display:flex;height:90vh;margin:0px;justify-content:center;align-items:center}.Help .text{display:flex;font-size:0.7em;flex-direction:column;justify-content:center;text-align:center;height:300px;width:800px}.Help .text h2{color:white;background:linear-gradient(to right, #17e9e0, #00e3e8, #00dcef, #00d5f4, #00cef7);padding:10px;border-radius:10px;text-shadow:2px 2px gray}@media only screen and (min-width: 1200px){.Help .text,.About .text{font-size:1em}}.groupElement{position:relative;display:inline-flex;flex-direction:column;align-items:center;height:80vh;margin:10px;width:350px;text-align:center;box-shadow:12px 12px 2px lightgray;border-radius:5px}.groupElement input{text-align:center}.groupElement .buttons-group{position:absolute;left:0px;display:flex;justify-content:space-evenly;background-image:linear-gradient(to bottom, #577cce, #425fb6, #2f439e, #1d2785, #09086b);align-items:center;height:100%;flex-direction:column;border-top-left-radius:5px;border-bottom-left-radius:5px;width:20%}.groupElement .buttons-group button{font-size:0.7em;font-weight:700;font-family:"montserrat";border:none;text-align:center;color:white;background:none;text-shadow:1px 1px gray;margin:0px;padding:0px;cursor:pointer}.groupElement .buttons-group button:hover{-webkit-transform:scale(1.2);transform:scale(1.2);transition:0.2s}.groupElement .question{position:absolute;bottom:0px}.groupElement .table-group{position:absolute;display:flex;flex-direction:column;height:200px;width:100%;overflow-x:hidden;top:50px}.groupElement h1{border-top-left-radius:5px;border-top-right-radius:5px;display:flex;font-size:1em;height:50px;width:100%;background-image:linear-gradient(to right, #577cce, #425fb6, #2f439e, #1d2785, #09086b);color:white;text-shadow:1px 1px gray;font-weight:bold;align-items:center;margin:0px;justify-content:center;z-index:2}.CreateGroup{display:flex;height:90vh;flex-wrap:wrap;justify-content:space-evenly;padding-top:50px}.CreateGroup .addGroup{position:absolute;left:5px;color:white;background-image:linear-gradient(to right, #577cce, #425fb6, #2f439e, #1d2785, #09086b);margin-left:20px;text-shadow:1px 1px gray;cursor:pointer;top:60px;padding:10px;width:120px;box-shadow:2px 2px 3px gray;border-radius:5px;margin-bottom:20px}.CreateGroup .addGroup:hover{-webkit-transform:scale(1.1);transform:scale(1.1);transition:0.2s}.CreateGroup .addGroup:active{-webkit-transform:scale(0.9);transform:scale(0.9);transition:0.2s}.CreateGroup .addedGroups{display:flex;flex-direction:row;flex-wrap:wrap;height:90vh;width:100%}.loading{position:absolute;color:#fccd04;font-family:"Montserrat";height:100px;width:100px;left:50%;margin-left:50px;top:50%;margin-top:50px}.members{position:absolute;right:10px;top:80px;height:45vh;overflow:scroll;width:250px}.members table{font-size:0.8em;font-family:"Montserrat"}.members table td{box-shadow:1px 1px 3px gray;padding:3px}@media only screen and (min-width: 600px){.members::-webkit-scrollbar{display:none}.members{-ms-overflow-style:none;scrollbar-width:none}}.question{position:absolute;bottom:0px;right:10px;height:30%;width:250px;font-size:0.7em}p,h1,h2,h3,h4,table{font-family:"Montserrat"}

